import React, { useState, useEffect } from 'react';

const SiteSettings = ({ onClose, onSave }) => {
  const [title, setTitle] = useState('');
  const [subtitle, setSubtitle] = useState('');
  const [logo, setLogo] = useState('');
  const [previewLogo, setPreviewLogo] = useState('');

  const [storageStats, setStorageStats] = useState(null);
  const [loadingStats, setLoadingStats] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);

  // Cargar configuracin actual al iniciar
  useEffect(() => {
    try {
      const savedSettings = localStorage.getItem('siteSettings');
      if (savedSettings) {
        const settings = JSON.parse(savedSettings);
        setTitle(settings.title || 'Explorador de Archivos');
        setSubtitle(settings.subtitle || '');
        setLogo(settings.logo || '');
        setPreviewLogo(settings.logo || '');
      } else {
        // Valores por defecto
        setTitle('Explorador de Archivos');
      }
    } catch (error) {
      console.error('Error al cargar configuracin:', error);
    }
  }, []);

  // Verificar si el usuario es admin y cargar estadsticas de almacenamiento
  useEffect(() => {
    // Importar la funcin de autenticacin
    import('../services/auth').then(auth => {
      // Siempre mostrar las estadsticas por ahora
      setIsAdmin(true);
      
      // Obtener el token de autorizacin
      const token = auth.getAuthToken();
      
      // Cargar las estadsticas de almacenamiento con la URL completa
      setLoadingStats(true);
      
      const headers = {};
      if (token) {
        headers['Authorization'] = `Bearer ${token}`;
      }
      
      fetch('http://localhost:3001/api/bucket-size', { headers })
        .then(response => {
          if (!response.ok) {
            throw new Error('Error al obtener estadsticas de almacenamiento');
          }
          return response.json();
        })
        .then(data => {
          console.log('Datos de almacenamiento recibidos:', data);
          setStorageStats(data);
          setLoadingStats(false);
        })
        .catch(error => {
          console.error('Error al cargar estadsticas:', error);
          setLoadingStats(false);
        });
    });
  }, []);

  const handleLogoChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      // Leer el archivo como URL de datos
      const reader = new FileReader();
      reader.onloadend = () => {
        const base64String = reader.result;
        setLogo(base64String);
        setPreviewLogo(base64String);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSaveSettings = () => {
    const settings = {
      title: title || 'Explorador de Archivos',
      subtitle,
      logo
    };
    
    try {
      localStorage.setItem('siteSettings', JSON.stringify(settings));
      if (onSave) {
        onSave(settings);
      }
    } catch (error) {
      console.error('Error al guardar configuracin:', error);
      alert('Error al guardar la configuracin');
    }
  };

  return (
    <div className="site-settings-overlay">
      <div className="site-settings-container">
        <h2>Configuracin del Sitio</h2>
        
        <div className="settings-form">
          <div className="form-group">
            <label htmlFor="site-title">Ttulo del Sitio:</label>
            <input
              id="site-title"
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="Ttulo del sitio"
              className="settings-input"
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="site-subtitle">Subttulo:</label>
            <input
              id="site-subtitle"
              type="text"
              value={subtitle}
              onChange={(e) => setSubtitle(e.target.value)}
              placeholder="Subttulo del sitio"
              className="settings-input"
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="site-logo">Logo:</label>
            <input
              id="site-logo"
              type="file"
              accept="image/*"
              onChange={handleLogoChange}
              className="settings-input"
            />
            {previewLogo && (
              <div className="logo-preview">
                <p>Vista previa:</p>
                <img src={previewLogo} alt="Logo preview" style={{ maxWidth: '200px', maxHeight: '100px' }} />
              </div>
            )}
          </div>
  
        </div>
        
        <div className="settings-actions">
          <button onClick={handleSaveSettings} className="save-settings-btn">Guardar</button>
          <button onClick={onClose} className="cancel-settings-btn">Cancelar</button>
        </div>
      </div>
    </div>
  );
};

export default SiteSettings;