import React, { useState, useRef, useEffect } from 'react';

const ConfigMenu = ({ 
  onToggleSettings, 
  onToggleUserManagement, 
  onToggleTagManager, 
  onToggleBackupManager,
  onToggleDashboard
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const menuRef = useRef(null);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  // Cerrar el men cuando se hace clic fuera de l
  useEffect(() => {
    function handleClickOutside(event) {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    }
    
    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  return (
    <div className="config-menu-container" ref={menuRef}>
      <button onClick={toggleMenu} className="config-menu-btn">
         Administracin
      </button>
      
      {isOpen && (
        <div className="config-menu-dropdown">
          <button onClick={() => {
            onToggleSettings();
            setIsOpen(false);
          }}>
             Configuracin del Sitio
          </button>
          
          <button onClick={() => {
            onToggleUserManagement();
            setIsOpen(false);
          }}>
             Gestin de Usuarios y Permisos
          </button>
          
          <button onClick={() => {
            onToggleTagManager();
            setIsOpen(false);
          }}>
             Administracin de Taxonomas y Etiquetas
          </button>
          
          <button onClick={() => {
            onToggleBackupManager();
            setIsOpen(false);
          }}>
             Copias de Seguridad y Restauracin
          </button>
          
          {onToggleDashboard && (
            <button onClick={() => {
              onToggleDashboard();
              setIsOpen(false);
            }}>
               Dashboard de Mtricas del Sistema
            </button>
          )}
        </div>
      )}
    </div>
  );
};

export default ConfigMenu;