import React, { useState, useEffect } from 'react';
import FileList from './FileList';
import FileActions from './FileActions';
import UploadForm from './UploadForm';
import SearchForm from './SearchForm';  // Importar el nuevo componente
import * as api from '../services/api';  // Importar todo el mdulo api
import { hasAdminPermission } from '../services/auth';  // Importar funcin de verificacin de permisos

const FileExplorer = ({ userRole, username }) => {
  const [currentPath, setCurrentPath] = useState('');
  const [files, setFiles] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isSearchMode, setIsSearchMode] = useState(false);  // Nuevo estado para modo de bsqueda
  const [searchResults, setSearchResults] = useState([]);   // Estado para resultados de bsqueda
  const [searchTerm, setSearchTerm] = useState('');         // Almacenar el trmino de bsqueda actual
  
  const [selectedFile, setSelectedFile] = useState(null);
  const [isSizeSearch, setIsSizeSearch] = useState(false);  // Estado para bsqueda por tamao

  const fetchFiles = async (path) => {
    if (isSearchMode) return; // No cargar archivos si estamos en modo bsqueda
    
    setIsLoading(true);
    setError(null);
    try {
      const data = await api.listFiles(path);  // Usar api.listFiles
      
      // Filtrar archivos .metadata para que no se muestren
      const filteredData = data.filter(file => {
        return !file.name.endsWith('.metadata') && 
               !file.name.endsWith('.youtube.metadata') && 
               !file.name.endsWith('.audio.metadata') &&
               !file.name.endsWith('.image.metadata') &&
               !file.name.endsWith('.access.metadata');
      });
      
      setFiles(filteredData);
    } catch (err) {
      console.error('Error al cargar archivos:', err);
      setError('No se pudieron cargar los archivos. Por favor intente nuevamente.');
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchFiles(currentPath);
  }, [currentPath, isSearchMode]);

  const handleNavigate = (newPath) => {
    // Si estamos en modo bsqueda, salir del modo bsqueda
    if (isSearchMode) {
      setIsSearchMode(false);
      setSearchResults([]);
      setSearchTerm('');
    }
    setCurrentPath(newPath);
  };

  const handleActionComplete = () => {
    // Refrescar la lista de archivos despus de una accin
    fetchFiles(currentPath);
  };

  // Funcin mejorada para manejar la bsqueda (normal, por etiquetas, por fecha y por tamao)
const handleSearch = async (term, isTagSearch = false, isDateSearch = false, dateSearchType = 'specific', isDirectResults = false, filterMetadata = true, isContentSearch = false, isSizeSearchResults = false) => {
  setIsLoading(true);
  setError(null);
  let results = [];
  
  try {
    // Verificar si ya recibimos resultados directos (para bsqueda combinada)
    if (isDirectResults) {
      console.log('Recibidos resultados directos de bsqueda combinada:', term.length);
      results = term; // En este caso, term contiene directamente los resultados
      setSearchTerm('Bsqueda combinada');
      setIsSizeSearch(isSizeSearchResults); // Actualizar estado para bsqueda por tamao
    }
    else if (isTagSearch) {
      console.log('Iniciando bsqueda por etiqueta:', term);
      setSearchTerm(term);
      setIsSizeSearch(false);
      
      // Indicar al usuario que la bsqueda puede tardar
      setError('Buscando archivos con la etiqueta. Esto puede tardar un momento...');
      
      // NUEVO: Usar la funcin optimizada del API
      results = await api.searchFilesByTag(term);
      console.log('Resultados finales de bsqueda por etiqueta:', results);
    }
    else if (isDateSearch) {
      console.log('Iniciando bsqueda por fecha:', term);
      setSearchTerm(term);
      setIsSizeSearch(false);
      
      // Indicar al usuario que la bsqueda puede tardar
      setError('Buscando archivos por fecha. Esto puede tardar un momento...');
      
      // Procesar parmetros de bsqueda por fecha
      let searchDate;
      let searchType;
      
      if (term.startsWith('date:')) {
        searchType = 'specific';
        searchDate = term.substring(5); // Eliminar 'date:' del inicio
      } else if (term.startsWith('month:')) {
        searchType = 'month';
        searchDate = term.substring(6); // Eliminar 'month:' del inicio
      } else if (term.startsWith('year:')) {
        searchType = 'year';
        searchDate = term.substring(5); // Eliminar 'year:' del inicio
      } else {
        throw new Error('Formato de fecha invlido');
      }
      
      console.log('Parmetros de bsqueda por fecha:', { searchType, searchDate });
      
      // NUEVO: Usar la funcin optimizada del API
      results = await api.searchFilesByDate(searchDate, searchType);
      console.log('Resultados finales de bsqueda por fecha:', results);
    }
    else if (isContentSearch) {
      console.log('Iniciando bsqueda por contenido:', term);
      setSearchTerm(term);
      setIsSizeSearch(false);
      
      // Indicar al usuario que la bsqueda puede tardar
      setError('Buscando en el contenido de los archivos. Esto puede tardar un momento...');
      
      // Ya tenemos los resultados directos de la bsqueda por contenido
      results = await api.searchByContent(term);
      
      // Marcar los resultados como encontrados por contenido para visualizacin
      results = results.map(file => ({
        ...file,
        foundByContent: true
      }));
      
      console.log('Resultados finales de bsqueda por contenido:', results);
    }
    else if (isSizeSearchResults) {
      console.log('Iniciando bsqueda por tamao:', term);
      setSearchTerm(term);
      setIsSizeSearch(true);
      
      // Para bsqueda por tamao, los resultados ya vienen directamente
      results = term;
    }
    else {
      // Bsqueda normal por nombre
      setSearchTerm(term);
      setIsSizeSearch(false);
      results = await api.searchFiles(term);
    }
  
    // Filtrar archivos de metadata si es necesario
    if (filterMetadata) {
      results = results.filter(file => {
        return !file.name.endsWith('.metadata') && 
               !file.name.endsWith('.youtube.metadata') && 
               !file.name.endsWith('.audio.metadata') &&
               !file.name.endsWith('.image.metadata') &&
               !file.name.endsWith('.access.metadata');
      });
      console.log('Resultados despus de filtrar archivos metadata:', results.length);
    }
  
    setSearchResults(results);
    setIsSearchMode(true);
    
    // Mostrar mensaje si no hay resultados
    if (results.length === 0) {
      if (isTagSearch) {
        setError(`No se encontraron archivos con la etiqueta "${term}"`);
      } else if (isDateSearch) {
        setError(`No se encontraron archivos para la fecha especificada`);
      } else if (isSizeSearchResults) {
        setError(`No se encontraron archivos con el tamao especificado`);
      } else {
        setError(`No se encontraron resultados para "${term}"`);
      }
    } else {
      // Limpiar el mensaje de error si hay resultados
      setError(null);
    }
    
    // Actualizar el estado de bsqueda por tamao si se recibieron resultados directos
    if (isDirectResults && isSizeSearchResults) {
      setIsSizeSearch(true);
    }
  } catch (err) {
    console.error('Error en la bsqueda:', err);
    setError(`Error al realizar la bsqueda: ${err.message}`);
    setIsSearchMode(false);
  } finally {
    setIsLoading(false);
  }
};

  // Funcin para obtener todos los archivos de forma recursiva
  const fetchAllFiles = async (path = '') => {
    try {
      const filesInPath = await api.listFiles(path);
      let allFiles = [];
      
      for (const file of filesInPath) {
        const filePath = path ? `${path}/${file.name}` : file.name;
        
        if (file.isFolder) {
          // Si es una carpeta, buscar recursivamente
          const filesInFolder = await fetchAllFiles(filePath);
          allFiles = [...allFiles, ...filesInFolder];
        } else {
          // Si es un archivo, agregarlo a la lista
          allFiles.push({
            ...file,
            path: filePath
          });
        }
      }
      
      return allFiles;
    } catch (error) {
      console.error('Error al obtener todos los archivos:', error);
      return [];
    }
  };
  
  // Funcin para buscar archivos por etiqueta
  const findFilesByTag = async (files, tagSearch) => {
    const results = [];
    const searchTermLower = tagSearch.toLowerCase();
    
    for (const file of files) {
      if (!file.isFolder) {
        try {
          // Obtener metadatos del archivo
          const metadata = await api.getFileMetadata(file.path);
          console.log(`Metadatos de ${file.path}:`, metadata);
          
          // Verificar si tiene etiquetas que coincidan
          if (metadata && metadata.tags && Array.isArray(metadata.tags)) {
            const hasMatchingTag = metadata.tags.some(tag => 
              tag.toLowerCase().includes(searchTermLower)
            );
            
            if (hasMatchingTag) {
              console.log(`Archivo coincidente encontrado: ${file.path}`);
              results.push({
                ...file,
                metadata: metadata // Incluir metadatos para mostrar etiquetas en resultados
              });
            }
          }
        } catch (error) {
          console.error(`Error al buscar etiquetas para ${file.path}:`, error);
        }
      }
    }
    
    return results;
  };

  // Funcin para buscar archivos por fecha
const findFilesByDate = async (files, dateSearch, searchType) => {
  const results = [];
  
  for (const file of files) {
    if (!file.isFolder) {
      try {
        // Obtener metadatos del archivo
        const metadata = await api.getFileMetadata(file.path);
        
        if (metadata && metadata.fileDate) {
          const fileDate = metadata.fileDate; // Formato esperado: YYYY-MM-DD
          
          let isMatch = false;
          
          // Verificar coincidencia segn el tipo de bsqueda
          if (searchType === 'specific') {
            // Bsqueda por fecha especfica (formato YYYY-MM-DD)
            isMatch = fileDate === dateSearch;
          } 
          else if (searchType === 'month') {
            // Bsqueda por mes y ao (formato MM-YYYY o slo MM si no tiene guion)
            let [searchMonth, searchYear] = dateSearch.includes('-') 
              ? dateSearch.split('-') 
              : [dateSearch, new Date().getFullYear().toString()];
              
            const fileYear = fileDate.split('-')[0];
            const fileMonth = fileDate.split('-')[1];
            
            isMatch = fileMonth === searchMonth && 
                      (searchYear === undefined || fileYear === searchYear);
          } 
          else if (searchType === 'year') {
            // Bsqueda slo por ao
            const fileYear = fileDate.split('-')[0];
            isMatch = fileYear === dateSearch;
          }
          
          if (isMatch) {
            console.log(`Archivo con fecha coincidente encontrado: ${file.path}`);
            results.push({
              ...file,
              metadata: metadata // Incluir metadatos para mostrar informacin en resultados
            });
          }
        }
      } catch (error) {
        console.error(`Error al buscar por fecha para ${file.path}:`, error);
      }
    }
  }
  
  return results;
};

  // Funcin para salir del modo bsqueda
  const handleClearSearch = () => {
    setIsSearchMode(false);
    setSearchResults([]);
    setSearchTerm('');
    setIsSizeSearch(false);
  };

  return (
    <div className="file-explorer">
      <h2>Explorador de Archivos</h2>
      
      <div className="explorer-header">
        <div className="current-path">
          <strong>Ubicacin actual:</strong> {currentPath || 'Raz'}
          {isSearchMode && (
            <>
              <span className="search-indicator">
                | Resultados para: "{searchTerm}"
              </span>
              <button onClick={handleClearSearch} className="clear-search-button">
                Volver
              </button>
            </>
          )}
        </div>
      </div>
      
      <SearchForm onSearch={handleSearch} isLoading={isLoading} />
      
      {error && <div className="error-message">{error}</div>}
      
      {isLoading ? (
        <div className="loading">Cargando...</div>
      ) : (
        <>
<FileList 
  files={isSearchMode ? searchResults : files} 
  currentPath={currentPath} 
  onNavigate={handleNavigate} 
  userRole={userRole}
  onActionComplete={handleActionComplete}
  isSearchResults={isSearchMode}
  isSizeSearch={isSizeSearch}  // Aadir esta prop
  onSelectFile={setSelectedFile}
  selectedFile={selectedFile}
/>
          
          {!isSearchMode && (
  userRole === 'admin' || 
  hasAdminPermission('create_folders') || 
  hasAdminPermission('delete_folders') || 
  hasAdminPermission('upload_files')
) && (
  <div className="admin-actions">
    {(userRole === 'admin' || 
     hasAdminPermission('create_folders') || 
     hasAdminPermission('delete_folders')) && (
      <FileActions 
  currentPath={currentPath} 
  onActionComplete={handleActionComplete} 
  selectedFile={selectedFile}  // Aadir esta prop
/>
    )}
    
    {(userRole === 'admin' || 
     hasAdminPermission('upload_files')) && (
      <UploadForm 
        currentPath={currentPath} 
        onUploadComplete={handleActionComplete} 
      />
    )}
  </div>
)}
        </>
      )}
    </div>
  );
};

export default FileExplorer;