// Funcin para obtener las cabeceras de autenticacin
const getAuthHeaders = () => {
  try {
    // Usar la misma funcin getAuthToken que usamos en fetchWithRetry
    const token = getAuthToken(); 
    console.log('[API-DEBUG] getAuthHeaders - Token obtenido:', token);
    
    if (token) {
      return {
        'Authorization': `Bearer ${token}`
      };
    } else {
      console.log('[API-DEBUG] getAuthHeaders - No hay token disponible');
    }
  } catch (error) {
    console.error('[API-DEBUG] Error al obtener informacin de autenticacin:', error);
  }
  
  return {};
};

// Funcin de utilidad para implementar reintentos en las llamadas API
const fetchWithRetry = async (url, options = {}, maxRetries = 3) => {
  let lastError;
  
  // Obtener el token de autorizacin
  const token = getAuthToken();

  // Para depuracin, mostrar la informacin completa del token
  try {
    console.log('[API-DEBUG] Token obtenido de getAuthToken:', token);
    
    if (!token) {
      console.log('[API-DEBUG] No hay token disponible para la peticin a:', url);
      console.log('[API-DEBUG] Estado de localStorage:', localStorage.getItem('user_session'));
    } else {
      const tokenData = JSON.parse(atob(token));
      console.log('[API-DEBUG] Token decodificado completo:', tokenData);
      console.log('[API-DEBUG] Enviando peticin con token:', {
        username: tokenData.username || 'No disponible',
        role: tokenData.role || 'No disponible',
        bucket: tokenData.bucket || 'No disponible'
      });
      
      // Verificar que el token tiene todas las propiedades necesarias
      if (!tokenData.username) {
        console.error('[API-DEBUG] ERROR: Token sin username');
      }
      if (!tokenData.bucket) {
        console.error('[API-DEBUG] ERROR: Token sin bucket');
      }
      if (!tokenData.role) {
        console.error('[API-DEBUG] ERROR: Token sin role');
      }
    }
  } catch (error) {
    console.error('[API-DEBUG] Error al decodificar token para depuracin:', error);
    console.error('[API-DEBUG] Token problemtico:', token);
  }
  // Aadir el token a los headers si existe
  if (token) {
    options.headers = {
      ...options.headers,
      'Authorization': `Bearer ${token}`
    };
  }
  
  for (let attempt = 1; attempt <= maxRetries; attempt++) {
    try {
      console.log(`Intento ${attempt}/${maxRetries} para ${url}`);
      
      // Configurar un timeout ms largo
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 15000); // 15 segundos de timeout
      
      const response = await fetch(url, {
        ...options,
        signal: controller.signal
      });
      
      clearTimeout(timeoutId);
      
      if (!response.ok) {
        const errorText = await response.text().catch(() => 'Error desconocido');
        throw new Error(`Error ${response.status}: ${errorText}`);
      }
      
      return response;
    } catch (error) {
      lastError = error;
      console.error(`Intento ${attempt} fall:`, error.message);
      
      // Si no es el ltimo intento, esperar antes de reintentar
      if (attempt < maxRetries) {
        const delay = 1000 * attempt; // Espera exponencial: 1s, 2s, 3s
        console.log(`Esperando ${delay}ms antes de reintentar...`);
        await new Promise(resolve => setTimeout(resolve, delay));
      }
    }
  }
  
  // Si llegamos aqu, todos los intentos fallaron
  throw lastError;
};

/**
 * Lista los archivos en la ruta especificada
 * @param {string} path - Ruta a listar
 * @returns {Promise<Array>} - Lista de archivos y carpetas
 */
export const listFiles = async (path = '') => {
  try {
    console.log(`Listando archivos en: ${path || 'raz'}`);
    
    const url = `${BASE_URL}/files${path ? `?prefix=${encodeURIComponent(path)}` : ''}`;
    const response = await fetchWithRetry(url);
    
    const data = await response.json();
    console.log(`Listado exitoso: ${data.length} elementos encontrados`);
    return data;
  } catch (error) {
    console.error('Error en listFiles despus de reintentos:', error);
    throw new Error('No se pudieron cargar los archivos');
  }
};

/**
 * Sube un archivo al servidor
 * @param {File} file - Archivo a subir
 * @param {string} path - Ruta donde subir el archivo
 * @returns {Promise<Object>} - Respuesta del servidor
 */
export const uploadFile = async (file, path = '') => {
  const formData = new FormData();
  formData.append('file', file);
  formData.append('path', path);
  
  // Aadir la fecha del sistema como metadato
  const currentDate = new Date().toISOString().split('T')[0];
  formData.append('uploadDate', currentDate);
  formData.append('fileDate', currentDate);
  
  // Obtener el token de autorizacin
  const token = getAuthToken();
  const headers = {};
  
  if (token) {
    headers['Authorization'] = `Bearer ${token}`;
  }
  
  try {
    const response = await fetch(`${BASE_URL}/upload`, {
      method: 'POST',
      headers,
      body: formData
    });

    if (!response.ok) {
      throw new Error('No se pudo subir el archivo');
    }
    
    const responseData = await response.json();
    
    // Si la subida fue exitosa, inmediatamente actualizar metadatos con la fecha
    if (responseData && responseData.success && responseData.filePath) {
      try {
        // Crear metadatos con la fecha actual
        const currentDate = new Date().toISOString().split('T')[0];
        const fileMetadata = {
          uploadDate: currentDate,
          fileDate: currentDate,
          uploadedBy: 'admin1', // O usar la informacin del usuario actual
          tags: [],
          lastModified: currentDate
        };
        
        // Actualizar los metadatos del archivo recin subido
        const metadataResponse = await updateFileMetadata(responseData.filePath, fileMetadata);
        console.log('Metadatos iniciales establecidos automticamente:', metadataResponse);
      } catch (metadataError) {
        console.warn('Error al establecer metadatos iniciales:', metadataError);
      }
    }
    
    return responseData;
    } catch (error) {
      console.error('Error en uploadFile:', error);
      throw error;
    }
};

/**
 * Crea una carpeta en el servidor
 * @param {string} folderName - Nombre de la carpeta a crear
 * @returns {Promise<Object>} - Respuesta del servidor
 */
export const createFolder = async (folderName) => {
  try {
    // Obtener el token de autorizacin
    const token = getAuthToken();
    const headers = {
      'Content-Type': 'application/json'
    };
    
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    const response = await fetch(`${BASE_URL}/createFolder`, {
      method: 'POST',
      headers: headers,
      body: JSON.stringify({
        folderName
      })
    });

    if (!response.ok) {
      throw new Error('No se pudo crear la carpeta');
    }

    return await response.json();
  } catch (error) {
    console.error('Error en createFolder:', error);
    throw error;
  }
};

/**
 * Elimina un archivo o carpeta del servidor
 * @param {string} path - Ruta del elemento a eliminar
 * @param {boolean} isFolder - Indica si el elemento es una carpeta
 * @returns {Promise<Object>} - Respuesta del servidor
 */
export const deleteItem = async (path, isFolder = false) => {
  // Validar que la ruta no est vaca
  if (!path) {
    throw new Error('Se requiere una ruta para eliminar el elemento');
  }
  
  console.log('Intentando eliminar:', path, 'Es carpeta:', isFolder);
  
  try {
    // Obtener el token de autorizacin
    const token = getAuthToken();
    const headers = {
      'Content-Type': 'application/json'
    };
    
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }
    
    // Para carpetas, usamos el endpoint mejorado deleteFolder con mtodo DELETE
    if (isFolder) {
      const response = await fetch(`${BASE_URL}/deleteFolder?path=${encodeURIComponent(path)}`, {
        method: 'DELETE',
        headers: headers
      });
      
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.message || `Error ${response.status}`);
      }
      
      return await response.json();
    } else {
      // Para archivos seguimos usando el endpoint delete existente
      const response = await fetch(`${BASE_URL}/delete?path=${encodeURIComponent(path)}`, {
        method: 'DELETE',
        headers: headers
      });
      
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.message || `Error ${response.status}`);
      }
      
      return await response.json();
    }
  } catch (error) {
    console.error('Error al eliminar elemento:', error);
    throw error;
  }
};

/**
 * Obtiene la URL de visualizacin o descarga de un archivo
 * @param {string} path - Ruta del archivo
 * @param {boolean} forceDownload - Indica si se debe forzar la descarga
 * @returns {Promise<string>} - URL de visualizacin o descarga
 */

export const getDownloadUrl = async (path, forceDownload = false) => {
  try {
    // Verificar que la ruta no est vaca
    if (!path) {
      throw new Error('Se requiere una ruta para obtener la URL');
    }
    
    // Determinar el tipo de archivo por su extensin
    const isPDF = path.toLowerCase().endsWith('.pdf');
    const isImage = /\.(jpe?g|png|gif|bmp|webp)$/i.test(path);
    const isDOCX = path.toLowerCase().endsWith('.docx');
    const isViewable = isPDF || isImage || isDOCX;
    
    console.log('Es un archivo visualizable?', isViewable);
    
    // Obtener el token de autenticacin
    const token = getAuthToken();
    
    // Construir la URL con parmetros
    let url = `${BASE_URL}/download?path=${encodeURIComponent(path)}`;
    
    // Aadir parmetro de visualizacin si es necesario
    if (isViewable && !forceDownload) {
      url += '&view=true';
    }
    
    // Aadir el token como parmetro de query para asegurar que se use incluso en redirecciones
    if (token) {
      url += `&token=${encodeURIComponent(token)}`;
    }
    
    console.log('URL de descarga con token:', url);
    
    // Realizar la solicitud
    const response = await fetch(url);
    
    if (!response.ok) {
      throw new Error('No se pudo obtener la URL del archivo');
    }
    
    // Obtener la URL pblica del archivo
    const data = await response.json();
    
    if (!data.success || !data.publicUrl) {
      throw new Error('No se pudo generar la URL del archivo');
    }
    
    // Retornar la URL pblica
    return data.publicUrl;
  } catch (error) {
    console.error('Error en getDownloadUrl:', error);
    throw error;
  }
};

