/**
 * Transcribe un archivo MP3 a texto
 * @param {string} filePath - Ruta del archivo MP3 en el bucket
 * @param {boolean} deleteOriginal - Si se debe eliminar el archivo original despus de la transcripcin
 * @param {boolean} processWithGPT - Si se debe procesar la transcripcin con ChatGPT
 * @returns {Promise<Object>} - Resultado de la transcripcin
 */
export const transcribeAudio = async (filePath, processWithGPT, customPrompt = null, confirmDelete = false) => {
  try {
    // Obtener el token de autorizacin
    const token = getAuthToken();
    const headers = {
      'Content-Type': 'application/json'
    };
    
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    const response = await fetch(`${BASE_URL}/transcribe-audio`, {
      method: 'POST',
      headers: headers,
      body: JSON.stringify({ 
        filePath,
        processWithGPT,
        customPrompt,
        confirmDelete  // Agregar el parmetro confirmDelete
      }),
    });

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`Error ${response.status}: ${errorText}`);
    }

    const data = await response.json();
        
    // Corregir manualmente el nombre del archivo procesado si es igual al de la transcripcin
    if (processWithGPT && data.processedPath === data.transcriptionPath) {
      // Generar la ruta correcta del archivo procesado
      const basePath = data.transcriptionPath.substring(0, data.transcriptionPath.lastIndexOf('_'));
      data.processedPath = `${basePath}_acta_formatada.txt`;
    }

    return data;
  } catch (error) {
    console.error('Error transcribing audio:', error);
    throw error;
  }
};
