/**
 * Obtiene la lista de usuarios dinmicos del sistema
 * @returns {Promise<Array>} - Lista de usuarios
 */
export const getUsers = async () => {
  try {
    const token = getAuthToken();
    const headers = {};
    
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    // Obtener el bucket actual del token
    let currentBucket = null;
    try {
      if (token) {
        const tokenData = JSON.parse(atob(token));
        currentBucket = tokenData.bucket || null;
        console.log('[API-DEBUG] getUsers - Bucket actual del admin:', currentBucket);
      }
    } catch (error) {
      console.error('[API-DEBUG] Error al decodificar token en getUsers:', error);
    }

    // Aadir el bucket como parmetro de consulta para filtrar usuarios por bucket
    let url = `${BASE_URL}/admin/users`;
    if (currentBucket) {
      url += `?bucket=${encodeURIComponent(currentBucket)}`;
    }

    const response = await fetch(url, {
      headers: headers
    });
    
    if (!response.ok) {
      throw new Error('Error al obtener usuarios');
    }

    return await response.json();
  } catch (error) {
    console.error('Error en getUsers:', error);
    throw error;
  }
};

/**
 * Crea un nuevo usuario en el sistema
 * @param {Object} userData - Datos del usuario a crear
 * @returns {Promise<Object>} - Respuesta del servidor
 */
export const createUser = async (userData) => {
  try {
    const token = getAuthToken();
    const headers = {
      'Content-Type': 'application/json'
    };
    
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    // Decodificar el token para obtener informacin sobre el bucket del admin
    let adminBucket = null;
    try {
      if (token) {
        const tokenData = JSON.parse(atob(token));
        adminBucket = tokenData.bucket || null;
        console.log('[API-DEBUG] createUser - Bucket del admin:', adminBucket);
      }
    } catch (error) {
      console.error('[API-DEBUG] Error al decodificar token en createUser:', error);
    }

    // Aadir el bucket del admin a los datos del usuario si no tiene uno definido
    if (adminBucket && !userData.bucket) {
      userData.bucket = adminBucket;
      console.log('[API-DEBUG] createUser - Asignando bucket del admin al nuevo usuario:', adminBucket);
    }

    const response = await fetch(`${BASE_URL}/admin/create-user`, {
      method: 'POST',
      headers: headers,
      body: JSON.stringify(userData)
    });
    
    if (!response.ok) {
      throw new Error('Error al crear usuario');
    }

    return await response.json();
  } catch (error) {
    console.error('Error en createUser:', error);
    throw error;
  }
};
/**
 * Actualiza un usuario existente
 * @param {string|number} userId - ID del usuario a actualizar
 * @param {Object} userData - Nuevos datos del usuario
 * @returns {Promise<Object>} - Respuesta del servidor
 */

export const updateUser = async (userId, userData) => {
  try {
    const token = getAuthToken();
    const headers = {
      'Content-Type': 'application/json'
    };
    
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    // Aadir log para depurar los datos que se estn enviando
    console.log('[API] updateUser - Datos enviados al servidor:', JSON.stringify(userData, null, 2));
    
    // Verificar si hay permisos administrativos en el objeto
    if (userData.assigned_folders) {
      const hasPermissionsObj = userData.assigned_folders.some(
        folder => typeof folder === 'object' && folder.type === 'admin_permissions'
      );
      console.log('[API] updateUser - Objeto de permisos detectado:', hasPermissionsObj);
    }

    const response = await fetch(`${BASE_URL}/admin/update-user/${userId}`, {
      method: 'PATCH',
      headers: headers,
      body: JSON.stringify(userData)
    });
    
    if (!response.ok) {
      const errorText = await response.text();
      console.error('[API] Error al actualizar usuario:', errorText);
      throw new Error(`Error al actualizar usuario: ${errorText}`);
    }

    const responseData = await response.json();
    console.log('[API] updateUser - Respuesta del servidor:', JSON.stringify(responseData, null, 2));
    return responseData;

  } catch (error) {
    console.error('Error en updateUser:', error);
    throw error;
  }
};

/**
 * Elimina o desactiva un usuario
 * @param {string|number} userId - ID del usuario a eliminar/desactivar
 * @param {boolean} permanent - Si es true, elimina permanentemente el usuario, si es false, solo lo desactiva
 * @returns {Promise<Object>} - Respuesta del servidor
 */
export const deleteUser = async (userId, permanent = false) => {
  try {
    const token = getAuthToken();
    const headers = {};
    
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
    }

    // Aadir parmetro para indicar eliminacin permanente
    const url = `${BASE_URL}/admin/delete-user/${userId}${permanent ? '?permanent=true' : ''}`;
    
    const response = await fetch(url, {
      method: 'DELETE',
      headers: headers
    });
    
    if (!response.ok) {
      throw new Error('Error al eliminar usuario');
    }

    return await response.json();
  } catch (error) {
    console.error('Error en deleteUser:', error);
    throw error;
  }
};

// Funcin para actualizar nombre de usuario
export const updateUsername = async (userId, newUsername, bucket) => {
  try {
    const token = getAuthToken();
    const response = await fetch(`${API_BASE_URL}/api/admin/users/${userId}/rename`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify({
        newUsername,
        bucket
      })
    });
    
    if (!response.ok) {
      const errorData = await response.json();
      throw new Error(errorData.message || 'Error al actualizar nombre de usuario');
    }
    
    return await response.json();
  } catch (error) {
    console.error('Error al actualizar nombre de usuario:', error);
    throw error;
  }
};

